* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background-color: #fcfcfc;
    color: #222;
    font-family: Arial, Helvetica, sans-serif;
}

.main-sec {
    position: absolute;
    inset: 0;
    display: flex;
}

.clock-container {
    margin: auto;
    width: clamp(300px, 40vw, 600px);
    height: clamp(500px, 40vw, 600px);
    background-color: #fff;
    box-shadow: 12px 12px 15px #ccc;
    border-radius: 10px;
    border: 1px solid #ccc;
    position: relative;
}

.day-container {
    text-align: center;
    /* border: 1px solid black; */
    padding-block: 20px;
}

.day-container p {
    font-size: clamp(33px, 3vw, 50px);
    font-weight: bolder;
}

.day-container p::before {
    content: attr(data-content);
}

/* Digit Box Style */

.digit-box-group {
    display: flex;
    padding: 20px;
    padding-block: 30px;
    justify-content: center;
}

.digit-box {
    width: clamp(50px, 10vw, 60px);
    height: 100px;
    overflow: hidden;
}

.digit-box > p,
.divider {
    background-color: #fcfcfc;
    font-size: clamp(33px, 3vw, 50px);
    height: 100px;
    color: rgb(131, 2, 2);
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 900;
    border: 3px solid #222222a2;
    border-radius: 10px;
    transition: all 300ms;
}

.divider {
    border: none;
}

/* Qoutes Area */
.qoutes-area {
    padding: 15px;
}

.qoutes,
.author {
    font-size: clamp(18px, 1.5vw, 33px);
    font-weight: 700;
    margin-block: 1rem;
}
.qoutes {
    text-align: center;
}

.author {
    text-align: end;
    padding-inline: 3rem;
}

.greet-area {
    position: absolute;
    padding: 15px 10px;
    bottom: 25px;
    right: 20px;
    left: 20px;
    background-color: rgb(61, 61, 218);
    border-radius: 15px;
    text-align: center;
    color: #fff;
    font-size: clamp(18px, 1.5vw, 33px);
    font-weight: 700;
    margin-inline: 1rem;
}
